.songlyrics__div {
  max-height: 400px;
  overflow-y: scroll;
  display: flex;
  justify-content: center;
}
.songlyrics__div::-webkit-scrollbar {
  display: none;
}
.songlyrics__div > pre {
  color: white;
  padding: 10px;
}
.songlyrics__error {
  width: 200px;
  height: 200px;
}
pre::-webkit-scrollbar {
  display: none;
}

.songlyrics__error__content {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.songlyrics__error__content > p {
  color: white;
  font-size: 18px;
  text-shadow: none;
  padding: 10px;
}

.songlyrics__caption {
  display: flex;
  flex-direction: column;
}
.songlyrics__title {
  color: white;
  font-size: 22px;
  font-family: "Abel";
}
.songlyrics__artist {
  color: white;
  font-size: 16px;
  font-family: "Abel";
}
.songlyrics__image {
  height: 70px;
  padding-right: 10px;
}
.songlyrics__header {
  display: flex;
  justify-content: space-between;
  margin-bottom: 20px;
  max-width: 400px;
  min-width: fit-content;

  margin-left: 35%;
}

.songlyrics__header__icon {
  color: "white";
  width: 20px;
}
.songlyrics__download {
  background: none;
  border: none;
  color: white;
}
.songlyrics__data {
  display: flex;
}
