.row__genre {
  font-size: 40px;
  margin-left: 30px;
  text-transform: uppercase;
  color: white;
  margin-bottom: -2em;
  margin-top: 20px;
}
.row__row {
  display: flex;
  overflow-y: hidden;
  overflow-x: scroll;
  padding: 20px;
}
.row__row::-webkit-scrollbar {
  display: none;
}
.row__card {
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
  margin: auto;
  text-align: center;
  display: flex;
  flex-direction: column;
  background-color: #fff;
  width: 200px;
  transition: transform 450ms;
  margin: 10px;
  padding: 7px;
  /* -moz-user-focus: normal; */
}
.row__card:focus {
  transform: scale(1.08);
  opacity: 1;
}

.row__card:hover {
  transform: scale(1.08);
  opacity: 1;
}
.row__card > img {
  height: 180px;
}

.row__album__title {
  margin-top: 10px;
  display: flex;
}

.row__album__title > label {
  font-size: 18px;
}
