.search__row {
  display: flex;
  overflow-y: hidden;
  overflow-x: scroll;
  padding: 20px;
  flex-direction: column;
}
.search__row::-webkit-scrollbar {
  display: none;
}
.search__row__card {
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
  margin: auto;
  text-align: center;
  display: flex;
  flex-direction: column;
  background-color: #fff;
  width: 200px;
  transition: transform 450ms;
  margin: 10px;
  padding: 7px;
}

.search__row__card:hover {
  transform: scale(1.08);
  opacity: 1;
}
.search__row__card > img {
  height: 180px;
}

.search__row__album__title {
  margin-top: 10px;
  display: flex;
}

.search__row__album__title > label {
  font-size: 18px;
}

.search__current__page {
  display: flex;
}

.search__paginate {
  display: flex;
  justify-content: center;
  padding-top: 30px;
}
.search__label {
  font-size: 40px;
  margin-left: 30px;
  text-transform: uppercase;
  color: white;
  margin-bottom: -2em;
  margin-top: 20px;
}
.pagination {
  display: flex;
  /* justify-content: space-between; */
  list-style: none;
  cursor: pointer;
}

.pagination a {
  padding: 10px;
  border: 1px solid #d12475;
  color: #ffffff;
}

.pagination__link {
  font-weight: bold;
}

.pagination__link--active a {
  color: #fff;
  background: #7c7a7a;
}

.pagination__link--disabled a {
  color: rgb(198, 197, 202);
  border: 1px solid rgb(198, 197, 202);
}

ul li:first-child > a {
  border-radius: 20px;
  margin-right: 5px;
  background-color: #e21d5f;
}

ul li:last-child > a {
  border-radius: 20px;
  margin-left: 5px;
  background-color: #e21d5f;
}
h1 {
  color: white;
  display: flex;
  justify-content: center;
}

.search__musictype {
  color: #e634f2;
  font-size: 30px;
}
